var Vue = require('vue')

var VueRouter = require('vue-router')
var VueResource = require('vue-resource')
var Vuerify=require("vuerify");
Vue.use(VueResource)
Vue.use(VueRouter)
Vue.use(Vuerify)


// es6
var header=require('./view/componets/header.vue');
var nav=require('./view/componets/nav.vue');
var index = require('./view/index.vue')
var login = require('./view/login.vue')
var register=require('./view/register.vue');
var forgetpwd=require('./view/forgetpwd.vue');//
var service=require('./view/service/service.vue');
var space=require('./view/space/space.vue');
var decorate=require('./view/decorate/decorate.vue');
var main=require('./view/user/main.vue');//
var setting=require('./view/user/setting.vue'); //
var myservice=require('./view/user/myservice.vue'); //
var wallte=require('./view/user/wallte.vue'); //
var info=require('./view/user/info.vue'); //
var address=require('./view/user/address.vue'); //
var apply=require('./view/user/apply.vue'); //
var restpassword=require('./view/user/restpassword.vue'); //
var maintenance_lists= require('./view/home/maintenance/lists.vue'); //
var maintenance_order= require('./view/home/maintenance/order.vue'); //
var transfer_lists= require('./view/home/transfer/lists.vue'); // 
var transfer_order= require('./view/home/transfer/order.vue'); // 
var pay_lists= require('./view/home/pay/lists.vue'); // 
var pay_order= require('./view/home/pay/order.vue'); // 
var more_lists= require('./view/home/more/lists.vue'); // 
var more_order= require('./view/home/more/order.vue'); // 
var label= require('./view/user/label.vue'); // 
var message= require('./view/user/message.vue'); //
var order= require('./view/user/order/order.vue'); //
var profile= require('./view/user/profile.vue'); //
var suggestion= require('./view/user/suggestion.vue'); //
var user_order=require('./view/user/order/order.vue'); //
var user_order_all=require('./view/user/order/all_order.vue'); //
var user_order_untaking=require('./view/user/order/untaking_order.vue'); //
var user_order_unservice=require('./view/user/order/unservice_order.vue'); //
var user_order_unconfirm=require('./view/user/order/unconfirm_order.vue'); //
var user_order_unevaluate=require('./view/user/order/unevaluate_order.vue');
//debug
//Vue.config.debug = true;

// 
var App = Vue.extend({
    components: {
    }
});

var router = new VueRouter(
    {
        mode:"history",
        hashbang: false,
        //history: true
    }
)
//  Vue.extend
// 
router.map(
    //
    {
    '/index':{//
        name:'index',//
        component:index,//`import`
        ////// 
        //subRoutes: {
        //    '/header': {
        //        component: header
        //    },
        //    '/nav': {
        //        component: nav
        //    }
        //}
    },
    //
    '/login': {
        name:'login',
        component:login
    },
    //
    '/register': {
        name:'register',
        component:register
    },
     '/forgetpwd': {
        name:'forgetpwd',
        component:forgetpwd
    },
    //
    '/service': {
        name:'service',
        component:service
    },
    //
    '/space': {
        name:'space',
        component:space
    },
    //
    '/decorate': {
        name:'decorate',
        component:decorate
    },
    '/user/main': {
        name:'main',
        component:main
        },
    '/user/setting':{
        name:'setting',
        component:setting
    },
    '/user/info':{
        name:'info',
        component:info
    },
     '/user/restpassword':{
        name:'restpassword',
        component:restpassword
    },//
    '/user/address':{
        name:'address',
        component:address
    },//
     '/user/apply':{
        name:'apply',
        component:apply
    },//
    '/user/myservice':{
        name:'myservice',
        component:myservice
    },//
    '/user/wallte':{
        name:'wallte',
        component:wallte
    },//
    '/maintenance/lists':{
        name:'maintenance_lists',
        component:maintenance_lists
    },
    '/maintenance/order/:serviceId/:serviceName':{
        name:'maintenance_order',
        component:maintenance_order
    },
    '/transfer/lists':{
        name:'transfer_lists',
        component:transfer_lists
    },
    '/transfer/order/:serviceId/:serviceName':{
        name:'transfer_order',
        component:transfer_order
    },
	'/pay/lists':{
        name:'pay_lists',
        component:pay_lists
    },
    '/pay/order/:serviceId/:serviceName':{
        name:'pay_order',
        component:pay_order
    },
    '/more/lists':{
        name:'more_lists',
        component:more_lists
    },
    '/more/order/:serviceId/:serviceName':{
        name:'more_order',
        component:more_order
    },
    '/user/label':{
        name:'label',
        component:label
    },
    '/user/message':{
        name:'message',
        component:message
    },
    '/user/profile':{
        name:'profile',
        component:profile
    },
    '/user/suggestion':{
        name:'suggestion',
        component:suggestion
    },
    '/user/order':{
        name:'order',
        component:user_order,
        //// 
        subRoutes: {
            'all': {
                component: user_order_all
            },
            'untaking': {
                component: user_order_untaking
            },
            'unservice': {
                component: user_order_unservice
            },
             'unconfirm': {
                component: user_order_unconfirm
            },
             'unevaluate': {
                component: user_order_unevaluate
            }
        }
    },
});
router.redirect({//
    '*':"/index"///index
});
// 
//  App  #app 
router.start(App, '#app');



