<template>
    <header class="mui-bar mui-bar-nav">
        <a class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"  onclick="window.history.go(-1)"></a>
        <h1 class="mui-title"></h1>
    </header>
    <div class="mui-content">
        <form  class="mui-input-group">
            <div class="mui-input-row">
                <input  type="text" class="mui-input-clear mui-input" placeholder="" v-model="mobile" >
            </div>
            <div class="mui-input-row">
                <input  v-model="pwd" type="password" class="mui-input-clear mui-input" placeholder="">
            </div>
            <div class="mui-input-row">
                <input  type="password" class="mui-input-clear mui-input" placeholder="" >
            </div>
            <div class="mui-input-row" style='position:relative;'>
                <input type="number" class="mui-input-clear mui-input" placeholder="" v-model="inputCode">
                <input v-model="strtimer" type="button" style='background-color: #CCCCCC; position:absolute;right:10px;top:2px; width: auto' class="mui-btn getnumber"   v-on:click="startTimer" >
                <input v-model="timer" type="hidden">
            </div>
        </form>
        <div class="mui-content-padded">
            <button class="mui-btn mui-btn-block" style='background:#ff8112;color:#fff;' v-on:click="asyRegister"></button>
            <span class="error" v-text="$parent.$vuerify.$errors[field]">1111</span>
        </div>

    </div>
    <div class="mui-content-padded oauth-area">
    </div>
    </div>
</template>
<script>
    export default {
    data () {
            return {
                strtimer:"",
                timer: 30,       //30
                stop : false,   //false
                Interval:null,  //setInterval
                mobile:"",
                pwd:"",
                inputCode:""
            }
    },
    vuerify: {
        mobile: {
            test: /\w{4,}/,  // 
                    message: ' 4 '
        },
        pwd: 'required' // 
    },
    computed:{
        errors (){
            return this.$vuerify.$errors
        }
    },
    methods : {
        update(){
            if(this.timer> 0) {
                this.timer--;
                this.strtimer=this.timer+""
            }else{
                this.strtimer="";
                this.stop=true;
            }
        },
        startTimer(){
            //falsetrue
            this.timer=30;
            if(this.stop == false) {
                this.$http.post(
                        'http://106.14.27.89:8001/api/GetServiceApiResult',
                    {
                        parameters:{
                             "CultureName":"",
                             "Mobile":this.mobile,
                             "VerifiationCCodeType":"1",
                             "ImageNo":"",
                             "InputCode":""
                        },
                        foreEndType:"2",
                        code:"10000002"
                    }
                    ).then(function(response) {
                            this.Interval = setInterval(this.update,1000);
                     }, function(response) {

                     });
            }
            else {
                clearInterval(this.Interval);
            }
            this.stop = !this.stop;
        },
        asyRegister(){
            if (this.$vuerify.check()) {
                this.$http.post(
                        'http://106.14.27.89:8001/api/GetServiceApiResult',
                        {
                            parameters: {
                                "CultureName": "",
                                "Mobile": this.mobile,
                                "VerifiationCCodeType": "1",
                                "ImageNo": "",
                                "InputCode": this.inputCode,
                                "name": "kaka",
                                "phone": this.mobile,
                                "pwd": this.pwd,
                            },
                            foreEndType: "2",
                            code: "10000004"
                        }
                ).then(function (response) {
                            var reslute = JSON.parse(response.data);
                            if (reslute.success) {
                                this.$route.router.go({name: "main"})
                            } else {
                                alert(reslute.message);
                            }

                        }, function (response) {
                            alert("ajax");
                        });
            }
        }
    }
    }
</script>



