<template>
    <header1></header1>
    <nav1></nav1>

    <div class="mui-content" style='margin-top:10px;'>
        <header class="mui-bar mui-bar-nav">
            <a class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left" onclick="window.history.go(-1)"></a>
            <h1 class="mui-title"></h1>
        </header>
        <form  class="mui-input-group">
            <div class="mui-input-row">
                <input type="text" class="mui-input-clear mui-input" placeholder="" v-model="phone">
            </div>
            <div class="mui-input-row" style='position:relative;'>
                <input type="password" class="mui-input-clear mui-input" placeholder="" v-model="pwd">
            </div>
        </form>
        <div class="mui-content-padded" style='text-align:center;'>
            <button  class="mui-btn mui-btn-block" style='background:#ff8112;color:#fff;' v-on:click="asyLogin" ></button>

            <div class="link-area"><a v-link="{ path: '/register'}"></a> <span class="spliter">|</span> <a v-link="{ path: '/forgetpwd'}"></a>
            </div>

        </div>
    </div>

</template>
<script>
    import header1 from './componets/header.vue'
    import nav1 from './componets/nav.vue'
    import alert from '../../src/components/alert.vue'

    export default {
        components : {
            header1,
            nav1,
            alert,
        },
        data () {
        return {
            phone:"",
            pwd:"",

            show : false,
            title : '',
            content : '',
        }
    },
    route:{
//            $.ajax(....,function(data){
//                 this.data = {"name":"1233"};
//            });
    },
    created: function () {
        //watcher/
        // DOM $el ,,this.a, 
        console.log("");
    },
    ready: function () {
        // $el  attached  Vue  vm.$appendTo()  ready 
        console.log("");
    },
    methods:{
        asyLogin:function(){
            this.$http.post(
                    'http://106.14.27.89:8001/api/GetServiceApiResult',
                    {
                        parameters:{
                            "phone":this.phone,
                            "pwd":this.pwd
                        },
                        foreEndType:"2",
                        code:"10000005"
                    }
            ).then(function(response) {
                        var  reslute=JSON.parse(response.data);
                        if(reslute.success){
                            this.$route.router.go({name:"main"})
                        }else{
                            this.content=reslute.message;
                            this.show=true;
                        }
                        debugger;
                        localStorage.setItem('userinfo', JSON.stringify(reslute.data));
                        console.info(JSON.parse(localStorage.getItem("userinfo")).fdname)
                    }, function(response) {

                    });
        }
    }
    }
</script>



